$font: 'NT Somic', Arial, Helvetica, sans-serif;
$std-size: 1.6rem;

$black: #0A0A0A;
$black-light: #202020;
$grey: #B6B6B6;
$grey1: #F5F5F5;
$grey2: #F0F0F0;
$grey3: #DBDBDB;
$grey-dark1: #B6B6B6;
$grey-dark2: #8A8A8A;
$grey-soft: #ECECEC;
$pink: #FF197A;
$white: #FAFAFA;
$yellow: #FFCF4B;
$violet: #8123FF;
$violet-bg: #D5BBF9;
$beige: #F88D8D;
$beige-bg: #FFF3F3;
$coral-bg: #FF9A9A;
$scarlet-bg: #FF573D;
$green-bg: #3DCF92;

html {
  font-size: 62.5%; }

body {
  width: 100%;
  margin: 0 auto;
  font-family: $font;
  font-weight: 400;
  font-size: $std-size;
  line-height: $std-size;
  color: $black;
  background-color: $white; }

h1, h2, h3, h4, h5, h6 {
  font-family: $font;
  font-weight: 700;
  color: $black; }

h1 {
  font-size: 6rem;
  line-height: 6rem; }

h2 {
  font-size: 4rem;
  line-height: 4rem; }

h3 {
  font-size: 2.8rem;
  line-height: 2.8rem; }

h4 {
  font-size: 1.6rem;
  line-height: 1.6rem; }

h5 {
  font-size: 1.4rem;
  line-height: 1.4rem; }

h6 {
  font-size: 1.2rem;
  line-height: 1.2rem; }

.header, .main, .footer {
  width: 100%; }

.header__top {
  background-color: $black-light;
  color: $grey1; }

.header__top__container,
.header__bottom__container,
// .banner__container,
.banner__container__item__content,
.categories__tiles,
.hero__container,
// .brands__list,
.suggestions__container,
.sets,
.utp,
.footer__navigation,
.footer__credentials {
  max-width: 135rem;
  margin: 0 auto; }

.header__top__container {
  display: flex;
  justify-content: space-between; }

.header__top__container__menu, .header__top__container__getintouch {
  display: flex;
  gap: 4rem;
  padding: 0.8rem 0; }

.header__top__container__menu__item__link,
.header__top__container__getintouch__item__city,
.header__top__container__getintouch__item__phone,
.header__top__container__getintouch__item__account,
.header__services__list__item__link {
  color: $grey1;

  &:hover {
    color: $pink; } }

.header__top__menu__item__link,
.header__top__getintouch__item__city,
.header__top__getintouch__item__phone,
.header__top__getintouch__item__account {
  font-size: 1.4rem;
  line-height: 1.8rem; }

.header__top__container__getintouch__item__city {
  text-decoration: underline;

  &:link, &:visited, &:hover {
    text-decoration: underline; } }

.header__top__container__getintouch__item__account {
  padding-left: 2.4rem;
  background-image: url("../images/icons/tzm-icon.svg");
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 1.6rem 1.6rem;

  &:hover {
    background-image: url("../images/icons/tzm-icon-pink.svg"); } }

.header__bottom {
  background-color: $black; }

.header__bottom__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 0.8rem 0; }

.header__bottom__container__column {
  display: flex;
  align-items: center; }

.column__left {
  justify-content: left; }

.column__center {
  justify-content: center; }

.column__right {
  justify-content: right; }

.header__bottom__container__column__button {
  padding: 1rem 1.8rem 1rem 1.8 + 1.6 + 0.5rem;
  color: $white;
  background-color: $pink;
  background-image: url("../images/icons/nav.svg");
  background-position: 1rem center;
  background-repeat: no-repeat;
  background-size: 1.6rem 1.6rem;
  border-radius: 0.4rem;
  cursor: pointer;

  &:hover {
    background-color: $black; } }


.header__bottom__container__column__logo__image {
  width: 9.2rem;
  height: 4.6rem; }

.header__services__list {
  display: flex;
  gap: 4rem; }

.header__services__list__item__link {
  padding-left: 2.8rem;
  font-size: 1.6rem;
  line-height: 2rem;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 2rem 2rem; }

.services-search {
  background-image: url("../images/icons/search.svg");

  &:hover {
    background-image: url("../images/icons/search-pink.svg"); } }

.services-favourites {
  background-image: url("../images/icons/heart.svg");

  &:hover {
    background-image: url("../images/icons/heart-pink.svg"); } }

.services-cart {
  background-image: url("../images/icons/cart.svg");

  &:hover {
    background-image: url("../images/icons/cart-pink.svg"); } }

.banner {
  position: relative;
  height: 45rem;
  width: 100%; }

.banner__container {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  // transition-property: transform
  // transform: translate3d(0, 0, 0)
  // transition-timing-function: var(--swiper-wrapper-transition-timing-function, initial)
  box-sizing: content-box; }

.banner__container__item {
  display: block;
  height: 45rem;
  width: 100%;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position-x: 50% !important;
  // flex-shrink: 0
  position: relative;
  // transition-property: transform
  // transform: translateZ(0)
  // backface-visibility: hidden
 }  // -webkit-backface-visibility: hidden

// .banner__container__item--active
//   width: 100%
//   background-size: cover
//   background-repeat: no-repeat
//   background-position-x: 50%

.banner__nav_buttons {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 135rem;
  height: 45rem; }

.banner__nav_buttons-next {
  background-image: url("../images/icons/arrow-right.svg");
  right: var(--swiper-navigation-sides-offset, 1rem);
  left: auto; }

.banner__nav_buttons-prev {
  background-image: url("../images/icons/arrow-left.svg");
  left: var(--swiper-navigation-sides-offset, 1rem);
  right: auto; }

.banner__nav_buttons-next, .banner__nav_buttons-prev {
  position: absolute;
  top: var(--swiper-navigation-top-offset, 50%);
  width: 2rem;
  height: 2rem;
  // margin-top: 50%
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center; }

.banner__container__item__content {
  width: 100%;
  text-align: center; }

.banner__container__item__content__heading {
  margin-top: 6rem;
  font-weight: 700;
  font-size: 6rem;
  line-height: 7.8rem;
  margin-bottom: 1rem; }

.banner__container__item__content__text {
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.6rem;
  margin-bottom: 3rem; }

.button {
  padding: 1rem 1.8rem;
  border-radius: 0.4rem;
  font-weight: 500;
  font-size: 1.4rem;
  line-height: 1.8rem; }

.button--pink {
  background-color: $pink;
  color: $white;

  &:hover {
    background-color: $black;
    color: $white; } }

.banner__dots {
  bottom: 2rem !important;
  top: auto !important;
  left: 0 !important;
  width: 100% !important;
  position: absolute;
  text-align: center;
  transition: .3s opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10; }

.banner__dots__item {
  display: inline-block;
  margin: 0 0.2rem;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 0.04rem;
  background-color: $grey3;
  cursor: pointer; }

.banner__dots__item.tns-nav-active {
  background-color: $pink; }

.categories {
  padding: 6rem 0; }

.categories__tiles {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem; }

.categories__tiles__item {
  width: 26.2rem;
  height: 34rem;
  background-color: $grey1;
  border-radius: 0.4rem;

  &:hover {
    background-image: url('../images/main-tiles/tile-bg-hover.svg');
    color: $white; } }

.categories__tiles__item__link {
  padding: 2rem;
  display: grid;
  grid: 20.5rem 6.8rem / 1fr;
  gap: 2rem;
  width: 100%;
  height: 100%; }

.categories__tiles__item__link__image {
  display: block;
  padding: 0.8rem; }

.categories__tiles__item__link__text {
  margin: auto;
  display: block;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 2.5rem;
  text-align: center; }

.hero {
  padding-bottom: 6rem; }

.brands {
  position: relative;
  margin: 6rem auto;
  width: 100%;
  max-width: 135rem;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; }

  &:hover .brands__nav_buttons__left,
  &:hover .brands__nav_buttons__right {
    display: block; } }

.brands__nav_buttons {
  position: absolute;
  top: 0;
  padding: 0 1rem;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  z-index: 10; }

.brands__nav_buttons__left {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-left-black.svg');
  cursor: pointer;
  opacity: 0.5;

  &:hover {
    opacity: 1; } }

.brands__nav_buttons__right {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-right-black.svg');
  cursor: pointer;
  opacity: 0.5;

  &:hover {
    opacity: 1; } }

.brands__list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  column-gap: 2rem; }

.brands__list__item {
  margin: 0;
  height: 9rem;
  width: auto; }

.brands__list__item__image {
  height: 9rem;
  width: auto;
  max-width: unset; }

.suggestions {
  margin-bottom: 6rem; }

.suggestions__container__heading {
  display: block;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.6rem; }

.suggestions__container {
  position: relative;

  &:hover .suggestions__container__nav_buttons__left,
  &:hover .suggestions__container__nav_buttons__right {
    display: block; } }

.suggestions__container__nav_buttons {
  position: absolute;
  top: 0;
  padding: 0 1rem;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: space-between; }


.suggestions__container__nav_buttons__left {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-left-black.svg');
  cursor: pointer;
  opacity: 0.5;
  z-index: 10;

  &:hover {
    opacity: 1; } }

.suggestions__container__nav_buttons__right {
  display: none;
  width: 4rem;
  height: 4rem;
  background-image: url('../images/icons/arrow-slider-right-black.svg');
  cursor: pointer;
  opacity: 0.5;
  z-index: 10;

  &:hover {
    opacity: 1; } }

.suggestions__container__list {
  display: flex;
  position: relative;
  flex-wrap: nowrap;
  align-items: stretch;
  column-gap: 1rem;
  overflow: hidden;
  z-index: 1;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; } }

.suggestions__container__list__item {
  position: relative;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2rem;
  width: 26.2rem;
  background-color: #fff;
  border-radius: 0.4rem; }

.suggestions__container .tns-item {
  display: flex !important;
  width: 26.2rem !important; }

.suggestions__container__list__item__images {
  margin: 2.2rem 2.2rem 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  width: 23rem;
  height: 23rem;
  overflow-x: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
      width: 0;
      height: 0; } }

.suggestions__container__list__item__images__link {
  display: block;
  width: 23rem;
  height: 23rem; }

.suggestions__container__list__item__images__link__image {
  max-width: unset;
  width: 23rem;
  height: 23rem; }

.suggestions__container__list__item__dots {
  display: flex;
  justify-content: flex-start;
  margin: 1rem 2.2rem 0;
  height: 0.6rem;
  gap: 0.4rem; }

.suggestions__container__list__item__dots__item {
  height: 0.6rem;
  width: 0.6rem;
  background-color: $grey3;
  cursor: pointer; }

.suggestions__container__list__item__dots__item.tns-nav-active {
  height: 0.6rem;
  width: 0.6rem;
  background-color: $pink;
  cursor: pointer; }

.suggestions__container__list__item__text {
  margin: 0 2.2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 2rem;
  flex: 1 0 10rem; }

.suggestions__container__list__item__text__title {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.8rem;
  width: 100%;

  &:hover {
    color: $pink; } }

.suggestions__container__list__item__text__info {
  display: flex;
  height: 2.2rem;
  justify-content: space-between; }

.suggestions__container__list__item__text__info__prices {
  display: flex;
  gap: 0.4rem;
  align-items: center; }

.suggestions__container__list__item__text__info__prices__price {
  font-size: 1.6rem;
  line-height: 130%;
  font-weight: 700;
  color: $black; }

.suggestions__container__list__item__text__info__prices__oldprice {
  font-size: 1.2rem;
  line-height: 130%;
  font-weight: 400;
  color: $grey-dark2;
  text-decoration: line-through; }

.suggestions__container__list__item__text__info__label {
  padding: 0.3rem 0.8rem;
  background-color: $grey1;
  border-radius: 0.4rem;
  color: $grey-dark2;
  font-size: 1.2rem;
  line-height: 130%;
  font-weight: 400; }

.suggestions__container__list__item__add_to_cart {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
  height: 5rem;
  text-align: center;
  align-content: center;
  border-radius: 0.4rem;

  &:hover {
    background-color: $pink; }

  &:hover .suggestions__container__list__item__add_to_cart__text {
    color: $white; }

  &:hover .suggestions__container__list__item__add_to_cart__icon {
    background-image: url("../images/icons/cart-white.svg"); } }

.suggestions__container__list__item__add_to_cart__text {
  color: $pink;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.6rem; }

.suggestions__container__list__item__add_to_cart__icon {
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  background-image: url("../images/icons/cart-pink.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 1.6rem 1.6rem; }

.suggestions__container__list__item__add_to_favourites {
  position: absolute;
  display: block;
  top: 1.6rem;
  right: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  z-index: 10;
  background-image: url("../images/icons/heart-grey.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    background-image: url("../images/icons/heart-pink.svg"); } }

.suggestions__container__list__item__add_to_favourites--added {
  position: absolute;
  display: block;
  top: 1.6rem;
  right: 1.6rem;
  width: 1.6rem;
  height: 1.6rem;
  z-index: 10;
  background-image: url("../images/icons/heart-pink-filled.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;

  &:hover {
    background-image: url("../images/icons/heart-grey.svg"); } }

.sets {
  margin-bottom: 6rem; }

.sets__heading {
  display: block;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.6rem; }

.sets__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  border-radius: 0.4rem; }

.sets__container__item {
  display: block;
  position: relative;
  background-color: $grey-soft;
  border-radius: 0.4rem;

  &:hover .sets__container__item__image {
    display: none; }

  &:hover .sets__container__item__image--hover {
    display: block; } }

.sets__container__item__title {
  position: absolute;
  display: block;
  padding: 3rem;
  font-size: 4rem;
  font-weight: 700;
  line-height: 4.4rem;
  color: $white; }

.sets__container__item__image {
  display: block; }

.sets__container__item__image--hover {
  display: none; }

.utp {
  margin-bottom: 6rem; }

.utp__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem; }

.utp__container__tile {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  text-align: center;
  background-color: $grey1;
  border-radius: 0.4rem; }

.utp__container__tile__image {
  display: block;
  width: 14.6rem;
  height: 14.6rem;
  align-self: center; }

.utp__container__tile__heading {
  display: block;
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.6rem; }

.utp__container__tile__text {
  display: block;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.1rem; }

.footer {
  background-color: $black;
  padding: 4.8rem; }

.footer__navigation {
  margin-bottom: 10rem;
  display: flex;
  justify-content: space-between;
  color: $white;
  text-transform: uppercase; }

.footer__navigation__heading {
  display: block;
  margin-bottom: 2rem;
  opacity: 1;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 2.1rem; }

.footer__navigation__menu {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 2rem;
  opacity: 0.7;
  text-transform: uppercase; }

.footer__navigation__catalogue__menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 7rem; }

.footer__navigation__menu__link {
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.7rem; }

.footer__credentials {
  display: flex;
  justify-content: space-between;
  color: $white;
  opacity: 0.5;
  text-transform: uppercase; }

.flex-left {
  align-self: left; }

.flex-center {
  align-self: center; }

.flex-right {
  align-self: right; }
